import axios from 'axios';
import { Button, Form } from 'react-bootstrap';
import { ArrowRightShort, BrightnessLowFill, Mailbox, Discord, Telegram } from 'react-bootstrap-icons';
import { useForm } from "react-hook-form";
import Swal from 'sweetalert2';
import { Title } from '@COM/Title'

import useFetch from '@HOOK/useFetch'



const About = () => {

  const { register, handleSubmit, reset, formState: { errors } } = useForm({
    defaultValue: {}
  });

  const Toast = Swal.mixin({
    toast: true,
    position: 'top-end',
    showConfirmButton: false,
    timer: 3000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener('mouseenter', Swal.stopTimer)
      toast.addEventListener('mouseleave', Swal.resumeTimer)
    }
  })


  const onSubmit = async (formData) => {
    let url = 'https://json-server-vercel-sepia.vercel.app/messages'
    await axios.post(url, {...formData, dt: Date.now()})
      .then(res => {
        Toast.fire({
          icon: 'success',
          title: '',
          html: `${formData.userName}<br/>
      `,
        })
        reset()
      }).catch(err => {
        Toast.fire({
          icon: 'error',
          title: 'Oops...',
          html: ""
        })
        console.error(err)
      })
  };
  return (
    <>
      <div className='container pb-3' style={{ maxWidth: "800px" }}>
        <Title cn="" jp="" />
        <Form className='d-flex flex-column ps-5 mb-5' onSubmit={handleSubmit(onSubmit)}>
          <Form.Group className="mb-3" controlId="formBasicName" >
            <Form.Floating>
              <Form.Control
                type="text"
                name="userName"
                placeholder=" / "
                {...register("userName", {
                  required: { value: true, message: "" }
                })}
              />
              <Form.Text className='text-danger'>
                {errors.userName?.message}
              </Form.Text>
              <label htmlFor="floatingInputCustom">
                 / 
              </label>
            </Form.Floating>
          </Form.Group>
          {/* -------------- */}
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Floating>
              <Form.Control
                type="text"
                name="email"
                placeholder=""
                {...register("email", {
                  required: { value: true, message: "email" },
                  pattern: { value: /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g, message: "email" }
                })}
              />
              <Form.Text className="text-danger">
                {errors.email?.message}
              </Form.Text>
              <label htmlFor="floatingInputCustom">
                
              </label>
            </Form.Floating>
          </Form.Group>
          {/* -------------- */}
          <Form.Group className="mb-3" controlId="formBasicTitle" >
            <Form.Floating>
              <Form.Control
                type="text"
                name="title"
                placeholder=""
                {...register("title")}
              />
              <label htmlFor="floatingInputCustom"></label>
            </Form.Floating>
          </Form.Group>
          {/* -------------- */}
          <Form.Group className="mb-3" controlId="formBasicMessage" >
            <Form.Floating>
              <Form.Control
                as="textarea"
                style={{ minHeight: "180px" }}
                name="message"
                placeholder=""
                {...register("message", {
                  required: { value: true, message: "" }
                })}
              />
              <Form.Text className="text-danger">
                {errors.message?.message}
              </Form.Text>
              <label htmlFor="floatingInputCustom">
                
              </label>
            </Form.Floating>
          </Form.Group>
          {/* -------------- */}
          <Button variant="primary" type="submit" style={{ width: "100px" }} className='align-self-end' >
            <span className='pe-1'></span>
            <ArrowRightShort size={25} />
          </Button>
        </Form>
        {/*  */}
        <section className='container py-5'>

          <div className="row justify-content-between"
            data-aos="fade-right"
          >

            <div className="col-12 col-md-6 py-5 px-4 mb-5 bg-light lh-base">
              <h3 className='fw-bold text-primary mb-2'></h3>
              <p className='mb-4'>
                <br />
                <br />
                <br />
                <br />
                <br />
                <br />
                <br />
                
              </p>
              <h3 className='fw-bold text-primary mb-2'></h3>
              <ul>
                <li>
                  <BrightnessLowFill size={20} className="me-2" />
                  
                </li>
                <li>
                  <BrightnessLowFill size={20} className="me-2" />
                  
                </li>
                <li>
                  <BrightnessLowFill size={20} className="me-2" />
                  
                </li>
                <li>
                  <BrightnessLowFill size={20} className="me-2" />
                  Sakenote
                </li>
                <li>
                  <BrightnessLowFill size={20} className="me-2" />
                  22 17
                </li>
                <li>
                  <BrightnessLowFill size={20} className="me-2" />
                  hex school 
                </li>
              </ul>
            </div>

            <div
              className="col-12 col-md-5 py-5 px-4 mb-5 bg-dark text-white col-4 d-flex flex-column justify-content-center lh-base"
              data-aos="fade-left"
            >
              <h3 className='fw-bold mb-2'></h3>
              <div className='rounded-circle my-2'
                style={{
                  backgroundImage: `URL("${process.env.PUBLIC_URL}/media/aki-icon.jpg")`,
                  backgroundPosition: "center",
                  backgroundSize: "cover",
                  width: "120px",
                  height: "120px",
                }}>
              </div>
              <div className='fw-lighter'>
                AKI CHENG<br />
                <br />
                <br />
                <br />
                <p></p>
                <ul>
                  <li className='mb-2'>
                    <Mailbox size={20} className="me-2" />
                    mailahsi.aki@gmail.com
                  </li>
                  <li className='mb-2'>
                    <Telegram size={20} className="me-2" />
                    telegram@ahsiaki
                  </li>
                  <li className='mb-2'>
                    <Discord size={20} className="me-2" />
                    Discordbakiii #2652
                  </li>
                </ul>
              </div>
            </div>

          </div>
        </section>

      </div>
    </>
  )
}

export default About